// react
import './index.css';
import ReactDom from 'react-dom';
import App from './App';
// redux
import { Provider } from 'react-redux';
import store from './store';
// router
import { BrowserRouter as Router } from 'react-router-dom';

ReactDom.render(
  <Router>
    <Provider store={store}>
      <App />
    </Provider>
  </Router>,
  document.getElementById('root')
)